  margin: 0;
  padding: 0;
  font-size: 16px;
  color: #fff;
  font-weight: 700;
}

.widget-body {
  padding: 20px;
}

.title-section {
  margin-bottom: 30px;
}
.title-section .heading {
  color: #fff;
  font-size: 20px;
  position: relative;
  padding-left: 30px;
}
.title-section .heading:before {
  position: absolute;
  content: "";
  width: 10px;
  top: 0;
  left: 0;
  bottom: 0;
  background: #ee1e46;
}

.custom-table {
  margin-bottom: 0;
}
.custom-table thead tr th {
  background: #ee1e46;
  color: #fff;
  border: none;
}
.custom-table tbody tr td {
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.custom-table tbody tr td strong {
  font-weight: 400;
}
.custom-table tbody tr:last-child td {
  border: none;
}

.footer-section {
  padding: 70px 0;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.5);
  background: #1a1e25;
}
.footer-section .widget h3 {
  color: #fff;
  font-size: 18px;
  margin-bottom: 20px;
}
.footer-section .widget .links li {
  margin-bottom: 10px;
}
.footer-section .widget .links li a {
  color: rgba(255, 255, 255, 0.5);
}
.footer-section .widget .links li a:hover {
  color: #ee1e46;
}

.custom-nav a {
  font-size: 30px;
  display: inline-block;
  width: 40px;
  height: 40px;
  position: relative;
  color: #fff;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.1);
}
.custom-nav a > span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.custom-nav a:hover {
  background-color: #ee1e46;
}

.owl-dots {
  position: absolute;
  width: 100%;
  bottom: -50px;
  text-align: center;
}
.owl-dots .owl-dot {
  display: inline-block;
}
.owl-dots .owl-dot > span {
  display: inline-block;
  background: rgba(255, 255, 255, 0.2);
  width: 20px;
  height: 3px;
  margin: 5px;
}
.owl-dots .owl-dot.active > span {
  background: #ee1e46;
}

.bg-light {
  background: rgba(255, 255, 255, 0.05) !important;
}

.bg-dark {
  background: #1a1e25 !important;
}

.custom-media .img {
  flex: 0 0 150px;
}
.custom-media .img img {
  max-width: 100%;
  border-radius: 7px;
}
.custom-media .text .meta {
  color: rgba(255, 255, 255, 0.3);
  font-size: 12px;
  font-weight: 700;
}
.custom-media .text h3 {
  font-size: 18px;
}
.custom-media .text h3 a {
  color: #fff;
}
.custom-media .text h3 a:hover {
  color: #ee1e46;
}

.custom-pagination a, .custom-pagination span {
  display: inline-block;
  color: #fff;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  line-height: 40px;
}
.custom-pagination a:hover {
  background: #fff;
  color: #ee1e46;
}
.custom-pagination span {
  background: #fff;
  color: #ee1e46;
}

.sidebar-box {
  margin-bottom: 30px;
  font-size: 15px;
  width: 100%;
  float: left;
}
.sidebar-box *:last-child {
  margin-bottom: 0;
}
.sidebar-box h3 {
  font-size: 18px;
  margin-bottom: 15px;
}

.categories li, .sidelink li {
  position: relative;
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px dotted rgba(255, 255, 255, 0.3);
  list-style: none;
}
.categories li:last-child, .sidelink li:last-child {
  margin-bottom: 0;
  border-bottom: none;
  padding-bottom: 0;
}
.categories li a, .sidelink li a {
  font-size: 18px;
  color: #fff;
  display: block;
}
.categories li a:hover, .sidelink li a:hover {
  color: #ee1e46;
}
.categories li a span, .sidelink li a span {
  position: absolute;
  right: 0;
  top: 0;
  color: #ccc;
}
.categories li.active a, .sidelink li.active a {
  color: #000;
  font-style: italic;
}

.comment-form-wrap {
  clear: both;
}

.comment-list {
  padding: 0;
  margin: 0;
}
.comment-list .children {
  padding: 50px 0 0 40px;
  margin: 0;
  float: left;
  width: 100%;
}
.comment-list li {
  padding: 0;
  margin: 0 0 30px 0;
  float: left;
  width: 100%;
  clear: both;
  list-style: none;
}
.comment-list li .vcard {
  width: 80px;
  float: left;
}
.comment-list li .vcard img {
  width: 50px;
  border-radius: 50%;
}
.comment-list li .comment-body {
  float: right;
  width: calc(100% - 80px);
}
.comment-list li .comment-body h3 {
  font-size: 20px;
}
.comment-list li .comment-body .meta {
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 0.1em;
  color: #ccc;
}
.comment-list li .comment-body .reply {
  padding: 5px 10px;
  background: #ee1e46;
  color: #fff;
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 700;
  border-radius: 4px;
}
.comment-list li .comment-body .reply:hover {
  color: #000;
  background: #e3e3e3;
}

.search-form .form-group {
  position: relative;
}
.search-form .form-group input {
  padding-right: 50px;
}
.search-form .icon {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

.post-meta {
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 0.2em;
}
.post-meta a {
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}